const len = resumeData.length

const Resume = ({ resumeRef }) => {


    useLayoutEffect(() => {

        let copyRef = document.getElementById('timeline')

        let isDown = false;
        let startX = 0;
        let scrollLeft = 0;

        if (copyRef) {

            copyRef.addEventListener('mousedown', (e) => {
                isDown = true;
                copyRef.classList.add('active');
                startX = e.pageX - copyRef.offsetLeft;
                scrollLeft = copyRef.scrollLeft;
            });

            copyRef.addEventListener('mouseleave', () => {
                isDown = false;
                copyRef.classList.remove('active');
            });
            copyRef.addEventListener('mouseup', () => {
                isDown = false;
                copyRef.classList.remove('active');
            });
            copyRef.addEventListener('mousemove', (e) => {
                if (!isDown) return;
                e.preventDefault();
                const x = e.pageX - copyRef.offsetLeft;
                const walk = (x - startX); //scroll-fast
                copyRef.scrollLeft = scrollLeft - walk;
            });
        }

        return () => {
            if (copyRef) {
                copyRef.removeEventListener("mousedown", null);
                copyRef.removeEventListener("mouseleave", null);
                copyRef.removeEventListener("mouseup", null);
                copyRef.removeEventListener("mousemove", null);
            }
        }
    }, [])



    return (
        <section className="resume" ref={resumeRef} >
            <div className="row">
                <div className="col text-align-center">
                    <h1 className='text-align-center section-title'>Timeline</h1>
                </div>
            </div>
            <GradCap className='resume__svg resume__svg-cap' />
            <Runner className='resume__svg resume__svg-runner' />
            <Work className='resume__svg resume__svg-work' />
            <Contact className='resume__svg resume__svg-contact' />
            <ResumeSvg className='resume__svg resume__svg-resume' />
            <div className="resume-container"
                id='timeline'
            >
                {resumeData.map((item, i) => (
                    <ResumeItem
                        item={item}
                        index={i}
                        len={len}
                        key={i}
                    />
                ))}
            </div>
        </section>
    )
}

export default Resume;