import React, { useState, useEffect, useRef } from 'react';
import { ReactComponent as Code } from './shapes/code.svg';
import { ReactComponent as Paint } from './shapes/paint-brush.svg';
import { ReactComponent as Laptop } from './shapes/laptop.svg';
import { ReactComponent as Microphone } from './shapes/microphone.svg';
import { ReactComponent as VectorHeader } from './shapes/Vector-header.svg';
import { ReactComponent as Avatar } from './images/avatar.svg';
import BooksAndBrews from './images/books-and-brewskies.jpeg';
import Habts from './images/habts/Habts-Home.png';
import HabtsHeader from './images/habts/Habts-Header.png'
import HabtsJar from './images/habts/Habts-Jar.png'
import HabtsReview from './images/habts/Habts-Review.png';
import LetsLinkLogin from './images/letslink/login.png';
import LetsLinkMap from './images/letslink/map.png';
import LetsLinkProfile from './images/letslink/profile.png';
import LetsLinkQuestion from './images/letslink/questions.png';
import LetsLinkList from './images/letslink/list.png';
import Resume from './components/Resume';
import PersonalSummary from './components/Summary';
import appData from './components/app-data';

import './App.scss';

const App = () => {
    const [name, setName] = useState();
    const [email, setEmail] = useState();
    const [message, setMessage] = useState();
    const [loading, setLoading] = useState(false);
    const [scrollPosition, setScrollPosition] = useState();
    const contactRef = useRef();
    const projectsRef = useRef();
    const expertiseRef = useRef();
    const resume = useRef();
    const [data, setData] = useState();


    useEffect(() => {
        //scroll listener
        window.addEventListener('scroll', () => {
            let pos = document.body.scrollTop || document.documentElement.scrollTop
            setScrollPosition(pos)
        })
    }, [])

    useEffect(() => {
        const url = window.location.hash.replace('#', '')
        const foundData = appData.find(data => data.url === url);
        if (foundData) {
            setData(foundData);
        }
    }, [])

    const onMenuItemClick = (name) => {
        switch (name) {
            case 'expertise':
                return window.scrollTo({
                    top: expertiseRef.current.offsetTop,
                    behavior: 'smooth'
                });
            case 'projects':
                return window.scrollTo({
                    top: projectsRef.current.offsetTop - 50,
                    behavior: 'smooth'
                });
            case 'contact':
                return window.scrollTo({
                    top: contactRef.current.offsetTop,
                    behavior: 'smooth'
                });
            case 'resume':
                return window.scrollTo({
                    top: resume.current.offsetTop - 100,
                    behavior: 'smooth'
                });
            default:
                window.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });
        }
    }

    const sendEmail = () => {
        if (!name || !message || !email) return alert('All fields required')
        setLoading(true)

        fetch("https://formsubmit.co/ajax/6fb9b6db755f974fe6a7d77b04c47d75", {
            method: "POST",
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify({
                name: name,
                message: message,
                email: email
            })
        })
            .then(response => response.json())
            .then(data => {
                console.log(data)
                setLoading(false)
                alert("Thank you for reaching out. I will respond as soon as I can.")
            })
            .catch(error => {
                console.log(error)
                alert('Sorry, something went wrong trying to send your request. Please try again.')
                setLoading(false)
            });
    }

    const isBelowHeader = () => {
        if (expertiseRef.current) {
            if (scrollPosition > expertiseRef.current.getBoundingClientRect().top) return true
        }
        return false;
    }

    return (
        <div className="container">
            <VectorHeader className='vector-header' />
            <nav className={`navbar flex ${isBelowHeader() ? 'background-white' : 'background-transparent'}`}>
                <div className="col-1 flex align-items-center" onClick={() => onMenuItemClick('')}>
                    <div className='logo position-relative'>
                        <div className="chevron chevron-initial" />
                        <div className="chevron chevron-flipped" />
                    </div>
                    <p className='logo-text'>Portfolio</p>
                </div>
                <div className="col-9 nav-divider" />
                <div className="col flex">
                    <div className="menu col flex justify-content-space-evenly pr-1">
                        <strong className="menu-item m-left-4" onClick={() => onMenuItemClick('expertise')}>Expertise</strong>
                        <strong className="menu-item m-left-4" onClick={() => onMenuItemClick('projects')}>Projects</strong>
                        <strong className="menu-item m-left-4" onClick={() => onMenuItemClick('resume')}>Resume</strong>
                        <strong className="menu-item m-left-4" onClick={() => onMenuItemClick('contact')}>Contact</strong>
                    </div>
                </div>
            </nav>

            <section className="header">
                <div className="col flex direction-column justify-content-center">
                    <div className="header-text">
                        <p>Hi! I'm a developer and welcome to my website!</p>
                        <h1>John</h1>
                        <h1>Nguyen</h1>
                    </div>
                </div>
                <div className="col flex justify-content-center">
                    <Avatar className='avatar' />
                </div>
            </section>

            {data && <PersonalSummary data={data} />}

            <section className={`expertise flex direction-column ${!data && 'd-curve'}`} ref={expertiseRef}>
                <div className="row">
                    <div className="col text-align-center">
                        <h1 className='text-align-center section-title'>My Expertise</h1>
                    </div>
                </div>
                <div className="expertise__content row direction-column">
                    <div className="row">
                        <div className="col flex justify-content-center">
                            <div className="expertise-item">
                                <Code />
                                <h2>Mobile Development</h2>
                                <p>I consider <u>JavaScript, HTML, and CSS</u> to be my specialty, but I also have experience with Python, Java, SQL, and even Visual Basic.</p>
                            </div>
                        </div>
                        <div className="col flex justify-content-center">
                            <div className="expertise-item">
                                <Laptop />
                                <h2>Desktop Development</h2>
                                <p>I dont program just to program, but I do it to create a masterpiece. <u>React</u> is my speciality.</p>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col flex justify-content-center">
                            <div className="expertise-item">
                                <Microphone className='custom-svg' />
                                <h2>Communication</h2>
                                <p>I value my relationships with my clients and colleagues, and I maintain healthy and honest relationships with them.</p>
                            </div>
                        </div>
                        <div className="col flex justify-content-center">
                            <div className="expertise-item">
                                <Paint className='custom-svg' />
                                <h2>Mobile and Web Design</h2>
                                <p>I dont consider myself artistic, but I know a beautiful piece when I see one. I design and create all of my projects.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

            <section className="projects" ref={projectsRef}>
                <div className="row">
                    <div className="col text-align-center">
                        <h1 className='text-align-center section-title'>My Projects</h1>
                    </div>
                </div>
                <div className="row projects__content direction-column">
                    <div className='projects__item-header'>
                        <h2>Booksandbrewskies</h2>
                        <a href='https://booksandbrewskies.herokuapp.com/' target="_blank" rel="noreferrer">
                            <p>
                                Visit here...
                        </p>
                        </a>
                    </div>
                    <p>
                        Is a ecommerce website. Main functionalities include; accepting payments by interacting with stripes API, dynamic cart, restricted admin page, and blog.
                    </p>
                    <div className="col">
                        <img src={BooksAndBrews} alt='booksandbrewskies' width='100%' />
                    </div>
                    <div className="col">
                        <div className='projects__item-header'>
                            <h2>LetsLink</h2>
                        </div>
                        <p>
                            Is a React Native App. Main functionalities include; algorithm to show users within a certain radius, create/update request, profile page including multiple image upload and caching mechanisms, and messaging.
                        </p>
                        <div className="row">
                            <div className="col">
                                <img src={LetsLinkLogin} alt='Letslink' className='img-main' />
                            </div>
                            <div className="col">
                                <img src={LetsLinkMap} alt='letslink login' />
                            </div>
                            <div className="col">
                                <img src={LetsLinkProfile} alt='letslink profile' />
                            </div>
                            <div className="col">
                                <img src={LetsLinkQuestion} alt='letslink questions' />
                            </div>
                            <div className="col">
                                <img src={LetsLinkList} alt='letslink list' />
                            </div>
                        </div>
                    </div>
                    <div className="col">
                        <div className='projects__item-header'>
                            <h2>Habts</h2>
                            <a href='https://habt-b0f23.web.app/' target="_blank" rel="noreferrer">
                                <p>
                                    Download here...
                                </p>
                            </a>
                        </div>
                        <p>
                            Is a React Native App. Main Functionlities include; create/update habits, notifications/reminders, reward system (badges), and reflection tool.
                        </p>
                        <div className="row">
                            <div className="col">
                                <img src={Habts} alt='Habts' className='img-main' />
                            </div>
                            <div className="col">
                                <img src={HabtsHeader} alt='habts header' />
                            </div>
                            <div className="col">
                                <img src={HabtsJar} alt='habts jar' />
                            </div>
                            <div className="col">
                                <img src={HabtsReview} alt='habts review' />
                            </div>
                            <div className="col"></div>

                        </div>
                    </div>
                </div>
            </section>


            <Resume resumeRef={resume} />

            <section className="contact" ref={contactRef}>
                <div className="row justify-content-center">
                    <div className='form'>
                        <h1 className='text-align-center section-title'>Contact Me!</h1>
                        <p>If you have any <u>project ideas</u>, want to <u>work with me</u>, or just have anything to say, fill out your <u>contact information</u> and a <u>brief message below</u>. I look forward to speaking with you.</p>

                        <label>
                            Name:
                                <input type='text' onChange={(e) => setName(e.target.value)} maxLength={100} placeholder='John Nguyen' />
                        </label>
                        <label>
                            Email:
                                <input type='text' onChange={(e) => setEmail(e.target.value)} maxLength={100} placeholder='john@nguyen.com' />
                        </label>
                        <label>
                            Message:
                                <textarea type='text' onChange={(e) => setMessage(e.target.value)} maxLength={500} placeholder="What's on your mind?" />
                        </label>
                        <button onClick={sendEmail}>{loading ? 'Sending..' : 'Send'}</button>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default App;