import { ReactComponent as Chevron } from '../shapes/chevron.svg';

const colorPrimary = '#004a7c';
const colorSecondary = '#005691';
const colorWhite = '#fafafa';
const colorGrey = '#E1E1E1'


const ResumeItem = ({ item, index, len }) => {

    const renderColors = (() => {
        let num = (index + 1);
        if (num % 3 === 0) {
            return {
                fill: colorPrimary,
                text: colorWhite
            }
        } else if (num % 2 === 0) {
            return {
                fill: colorGrey,
                text: colorPrimary
            }
        } else {
            return {
                fill: colorSecondary,
                text: colorWhite
            }
        }
    })()

    // const renderSvg = (() => {
    //     switch (item.svg) {
    //         case 'work':
    //             return <Work />
    //         case 'grad':
    //         default:
    //             return <GradCap />
    //     }
    // })()

    return (
        <div className='resume__item' style={{
            gridColumn: index + 1
        }}>
            <div className="resume__item-chevron" style={{
                fill: renderColors.fill
            }}>
                <strong className="resume__item-date"
                    style={{
                        color: renderColors.text
                    }}
                >
                    {item.date}
                </strong>
                <Chevron />
            </div>
            <hr className='dotted-line'
                style={{
                    borderLeft: `3px dashed ${renderColors.fill}`
                }}
            />
            <div className="resume__item-content"
                style={
                    (index + 1) % 2 === 0 ? {
                        flexDirection: 'column-reverse'
                    } : {
                            flexDirection: 'column'
                        }

                }
            >
                <h1 className="resume__item-header"
                    style={{
                        backgroundColor: renderColors.fill,
                        color: renderColors.text
                    }}
                >{item.header}</h1>
                <div className="resume__item-content-desc"
                    style={{
                        backgroundColor: renderColors.fill,
                        color: renderColors.text
                    }}
                >
                    <p>
                        {item.description}
                    </p>
                </div>
            </div>
        </div>
    )
}

export default ResumeItem;